.section {
  background-color: $menu-font-color;

  &__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;
    text-align: center;
    margin: 0;
    padding: 0;
    margin-bottom: 48px;

    @include onTablet {
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 64px;
    }

    &--align-left {
      text-align: left;
    }
  }

  &__text {
    margin: 0;
    padding: 0;
  }
}
